import React from 'react';
import { Program } from "./snowflake"
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';


interface Svg {
  code: string
}

function DisplaySvg(props: { image: Svg }) {
  return (
    <div dangerouslySetInnerHTML={{ __html: props.image.code }} />
  )
}


interface ViewerProps {
  program: Program | null
}

interface ViewerState {
  result: Svg | null
  seed: number,
  depth: number
}

class Viewer extends React.Component<ViewerProps, ViewerState> {

  state: ViewerState = {
    result: null,
    seed: 1,
    depth: 10
  }

  componentDidMount() {
  }

  componentDidUpdate(pprops: ViewerProps) {
    if (pprops.program !== this.props.program) {
      this.execute()
    }
  }

  handleSeedChange(event: any) {
    let value = parseInt(event.target.value)
    if (value) {
      this.setState({ seed: value })
    }
  }

  handleDepthChange(event: any) {
    let value = parseInt(event.target.value)
    if (value) {
      this.setState({ depth: value })
    }
  }

  execute() {
    if (this.props.program) {
      console.log("Executing")
      let result = this.props.program.execute({
        seed: this.state.seed,
        depth: this.state.depth
      })

      if (result) {
        this.setState({ result: { code: result } })
      }

      this.setState({ seed: this.state.seed + 1 })
    }
  }

  render() {
    let result;
    let buttons;

    buttons = [<Button onClick={() => this.execute()} key="0">Run</Button>]
    buttons.push(<TextField key="1" value={this.state.seed} onChange={(event) => this.handleSeedChange(event)} id="seed" type="number" label="Seed" variant="outlined" />)
    buttons.push(<TextField key="2" value={this.state.depth} onChange={(event) => this.handleDepthChange(event)} id="depth" type="number" label="Depth" variant="outlined" />)

    if (!this.state.result) {
      result = <i>Nothing to show yet</i>
    } else {
      result = <DisplaySvg image={this.state.result} />
    }

    return (
      <div id="viewer">
        <div>{buttons}</div>
        <div>{result}</div>
      </div>
    );
  }
}

export default Viewer;
