import * as monaco from 'monaco-editor/esm/vs/editor/editor.api';

import init, * as snow from "snowflakejs";


export class SnowCompiler {
  isReady: boolean = false


  initialize(): Promise<void> {
    return init().then(
      () => {
        snow.snow_initialize()
        this.isReady = true
      }
    )
  }

  compile(source: string): Program {
    const result = snow.validate("__web__", source)
    return new Program(result)
  }
}

export interface ProgramArgs {
  depth: number,
  seed: number
}

export class Program {
  program: snow.Program

  constructor(result: snow.Program) {
    this.program = result
  }

  hasError(): boolean {
    return snow.has_errors(this.program)
  }

  execute(args: ProgramArgs): string {
    const input = new snow.ProgramArgs(args.seed, args.depth)
    return snow.execute(this.program, input)
  }

  markers(): Array<monaco.editor.IMarkerData> {
    return snow.diagnostic_markers(this.program)
  }
}


export function snowflakeMonarchTokenizer(): monaco.languages.IMonarchLanguage {
  return {
    keywords: [
      'system', 'rule', 'fn', 'struct', 'using', 'if', 'let', 'match', 'sy', 'else', 'with', 'weight', 'yield', 'term', 'start', 'return'
    ],

    typeKeywords: [
      'boolean', 'double', 'byte', 'int', 'short', 'char', 'void', 'long', 'float'
    ],

    operators: [
      '=', '>', '<', '!', '~', '?', ':', '==', '<=', '>=', '!=',
      '&&', '||', '++', '--', '+', '-', '*', '/', '&', '|', '^', '%',
      '<<', '>>', '>>>', '+=', '-=', '*=', '/=', '&=', '|=', '^=',
      '%=', '<<=', '>>=', '>>>='
    ],

    // we include these common regular expressions
    symbols: /[=><!~?:&|+\-*/^%]+/,

    // C# style strings
    escapes: /\\(?:[abfnrtv\\"']|x[0-9A-Fa-f]{1,4}|u[0-9A-Fa-f]{4}|U[0-9A-Fa-f]{8})/,

    // The main tokenizer for our languages
    tokenizer: {
      root: [
        // identifiers and keywords
        [/[a-z_$][\w$]*/, {
          cases: {
            '@typeKeywords': 'keyword',
            '@keywords': 'keyword',
            '@default': 'identifier'
          }
        }],
        [/[A-Z][\w$]*/, 'type.identifier'],  // to show class names nicely

        // whitespace
        [/[ \t\r\n]+/, 'white'],

        // comment
        [/\/\/.*$/, 'comment'],

        // delimiters and operators
        [/[{}()[]]/, '@brackets'],
        [/[<>](?!@symbols)/, '@brackets'],
        [/@symbols/, {
          cases: {
            '@operators': 'operator',
            '@default': ''
          }
        }],

        // numbers
        [/\d*\.\d+([eE][-+]?\d+)?/, 'number.float'],
        [/#[0-9a-fA-F]+/, 'number.hex'],
        [/\d+/, 'number'],

        // delimiter: after number because of .\d floats
        [/[;,.]/, 'delimiter'],

        // strings
        [/"([^"\\]|\\.)*$/, 'string.invalid'],  // non-teminated string
        [/"/, { token: 'string.quote', bracket: '@open', next: '@string' }],
      ],

      string: [
        [/[^\\"]+/, 'string'],
        [/\\./, 'string.escape.invalid'],
        [/"/, { token: 'string.quote', bracket: '@close', next: '@pop' }]
      ],

      whitespace: [
      ],
    },
  }
}