import React from 'react';
import './App.css';

import Editor, { loader, Monaco } from "@monaco-editor/react";
import * as monaco from 'monaco-editor/esm/vs/editor/editor.api';
import { snowflakeMonarchTokenizer } from "./snowflake";
import Button from '@mui/material/Button';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import List from '@mui/material/List';
import ListItemText from '@mui/material/ListItemText';
import Viewer from "./Viewer"
import * as snow from "./snowflake"

const monacoOptions: monaco.editor.IEditorOptions = {
  scrollBeyondLastLine: false,
  minimap: {
    enabled: false
  }
};

type Example = {
  file: string, name: string
}

const examples: Array<Example> = [
  { file: "koch_snowflake.snow", name: "Koch Snowflake" },
  { file: "koch_snowflake_random.snow", name: "Koch Snowflake (Random)" },
  { file: "box_divide.snow", name: "Boxes" }
]

async function loadExample(filename: string): Promise<Response> {
  const path = "examples/" + filename
  console.log("Loading " + path)
  return fetch(path)
}

function ExamplesMenu(props: { onExample: (e: Example) => void, examples: Array<Example> }) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event: any) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = (example: Example | null) => {
    setAnchorEl(null);
    console.log(example)
    if (example) {
      props.onExample(example)
    }
  };

  let buttons = props.examples.map(example => <MenuItem key={example.name} onClick={() => handleClose(example)}>{example.name}</MenuItem>)

  return (
    <div>
      <Button
        id="basic-button"
        aria-controls={open ? 'basic-menu' : undefined}
        aria-haspopup="true"
        aria-expanded={open ? 'true' : undefined}
        onClick={handleClick}
      >
        Examples
      </Button>
      <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={() => handleClose(null)}
        MenuListProps={{
          'aria-labelledby': 'basic-button',
        }}
      >
        {buttons}
      </Menu>
    </div>
  );
}

function DiagnosticList(props: { diagnostics: Array<monaco.editor.IMarkerData> }) {
  let index = 0
  const items = props.diagnostics.map(marker => {
    let key = index
    index += 1
    return < ListItemText key={key}
      style={{ backgroundColor: "#faa" }}>
      {marker.startLineNumber}: {marker.startColumn} {marker.message}
    </ListItemText >
  })

  return <List>{items}</List>
}

interface AppState {
  program: snow.Program | null
  editor: monaco.editor.IStandaloneCodeEditor | null
  diagnostics: Array<monaco.editor.IMarkerData>
}

class App extends React.Component<{}, AppState> {
  state: AppState = {
    program: null,
    editor: null,
    diagnostics: new Array<monaco.editor.IMarkerData>()
  }

  compiler: snow.SnowCompiler = new snow.SnowCompiler()

  componentDidMount() {
    loader.init().then((monaco: Monaco) => {
      monaco.languages.register({
        "id": "snowflake"
      });
      monaco.languages.setMonarchTokensProvider("snowflake", snowflakeMonarchTokenizer())

      // init compiler
      this.compiler.initialize().then(
        () => this.handleShowExample(examples[0])
      )
    })
  }

  monacoMount(editor: monaco.editor.IStandaloneCodeEditor, monaco: Monaco) {
    this.setState({
      editor: editor
    })
  }

  handleEditorChange(value: any, event: any) {
    const source = this.state.editor?.getModel()?.getValue()
    if (source) {
      const result = this.compiler.compile(source)
      this.setState({ diagnostics: result.markers() })
    }
  }


  async compileCurrent() {
    const source = this.state.editor?.getModel()?.getValue()
    if (source) {
      const result = this.compiler.compile(source)

      if (result.hasError()) {
        // Only set diagnostics
        this.setState({ diagnostics: result.markers() })
      } else {
        // Set both if there are no errors
        this.setState({ program: result, diagnostics: result.markers() })
      }
    }
  }

  handleShowExample(example: Example) {
    if (this.state.editor) {
      loadExample(example.file).then(
        (response) => response.text()
      ).then(
        (code) => {
          console.log("Loaded example ", example.name)
          this.state.editor?.setValue(code)
          this.compileCurrent()
        }
      )
    }
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          Snowflake
        </header>
        <div className="content">
          <div id="editor">
            <Button onClick={() => this.compileCurrent()}>Compile</Button>
            <ExamplesMenu onExample={(e) => this.handleShowExample(e)} examples={examples} />
            <Editor
              height="80vh"
              defaultLanguage="snowflake"
              defaultValue=""
              options={monacoOptions}
              onMount={(editor, monaco) => this.monacoMount(editor, monaco)}
              onChange={(value, monaco) => this.handleEditorChange(value, monaco)}
            />
            <DiagnosticList diagnostics={this.state.diagnostics} />
          </div>
          <Viewer program={this.state.program} />
        </div>
      </div>
    );
  }
}

export default App;
